 let dslTableHeader = [
     [{
             colspan: 1,
             rowspan: 3,
             label: "日期",
         },
         {
             colspan: 5,
             rowspan: 1,
             label: "配送信息"
         }
     ],
     [{
             colspan: 1,
             rowspan: 2,
             label: "姓名",
         },
         {
             colspan: 4,
             rowspan: 1,
             label: "地址"
         }
     ],
     [{
             colspan: 1,
             rowspan: 1,
             label: "省份"
         },
         {
             colspan: 1,
             rowspan: 1,
             label: "市区"
         },
         {
             colspan: 1,
             rowspan: 1,
             label: "地址"
         },
         {
             colspan: 1,
             rowspan: 1,
             label: "邮编"
         }
     ]
 ];




 let tableHeaderTree = [];
 /* 第一行节点肯定是roots */
 /* 从根节点，递归 */
 dslTableHeader[0].map((tree, index) => {
     tableHeaderTree = fact(dslTableHeader, tableHeaderTree)

     function fact(dslTableHeader, tableHeaderTree) {
         /* 用来拆解的tableHeader */
         let currentTableHeader = JSON.parse(JSON.stringify(dslTableHeader));

         const headerInfo = ((currentTableHeader) => {
             let totalRow = currentTableHeader.length;
             let totalCol = 0;
             currentTableHeader[0].map(({
                 colspan
             }) => {
                 totalCol += colspan;
             });
             return {
                 totalRow,
                 totalCol
             }
         })(currentTableHeader)

         tree = currentTableHeader[0].shift();
         let treeItem = {};
         treeItem.label = tree.label

         if (tree.rowspan === headerInfo.totalRow) {
             return treeItem;
         } else {
             currentTableHeader.shift();
             return fact()
         }

     }
 })